@import "../../global.scss";
@font-face {
  font-family: Stigma;
  src: url("../../Stigma Display Typeface.ttf");
}

.Contact {
  height: 88%;
  width: 100%;
  background-color: $bgColour;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;

  h1 {
    font-family: Stigma;
    font-size: 80px;
    font-weight: 100;
    overflow: hidden;
    margin-bottom: 30px;
  }

  .wrapper {
    width: 80%;
    height: 100%;

    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    .left {
      display: flex;
      width: 100%;
      height: 100%;
      overflow: hidden;
      justify-content: center;
      align-items: center;
      flex: 1;

      img {
        width: 500px;
        margin-top: 0;
        margin-bottom: auto;
      }
    }

    .text {
      width: 100px;
      height: 100%;
      overflow: hidden;
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
      flex: 1;
      margin-bottom: 50px;

      .textbox {
        display: flex;
        justify-content: center;
        align-items: center;

        a {
          font-size: 25px;
          font-weight: 500;
          color: black;
          text-decoration: none;
          margin: 0 5px 5px 5px;
        }

        h3 {
          font-size: 0px;
        }
      }

      .textbox:hover {
        opacity: 0.7;
        h3 {
          font-size: 20px;
        }
      }
    }

    .right {
      display: flex;
      width: 100%;
      height: 100%;
      overflow: hidden;
      justify-content: center;
      align-items: center;
      flex: 1;

      img {
        display: flex;
        width: 500px;
        margin-top: 0;
        margin-bottom: auto;
        transform: scaleX(-1);
        align-items: flex-start;
      }
    }
  }
}

@media only screen and (max-width: 720px) {
  .Contact {
    height: 60%;
    width: 100%;
    background-color: $bgColour;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;

    h1 {
      font-family: Stigma;
      font-size: 16vw;
      font-weight: 100;
      overflow: hidden;
      margin-bottom: 30px;
    }

    .wrapper {
      width: 80%;
      height: 50%;

      display: flex;
      justify-content: start;
      align-items: center;
      overflow: hidden;

      .left {
        display: none;
      }

      .text {
        width: 100px;
        height: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: start;
        flex: 1;
        margin-bottom: 0px;

        .textbox {
          display: flex;
          justify-content: center;
          align-items: center;

          a {
            font-size: 25px;
            font-weight: 500;
            color: black;
            text-decoration: none;
            margin: 0 5px 5px 5px;
          }

          h3 {
            font-size: 0px;
          }
        }
      }

      .right {
        display: none;
      }
    }
  }
}

@media only screen and (min-width: 720px) and (max-width: 1200px) {
  .Contact {
    height: 60%;
    width: 100%;
    background-color: $bgColour;
    display: flex;
    flex-direction: column;
    justify-content: start;

    h1 {
      font-family: Stigma;
      font-size: 10vw;
      font-weight: 100;
      overflow: hidden;
      margin-bottom: 30px;
    }

    .wrapper {
      width: 80%;
      height: 50%;

      display: flex;
      justify-content: start;
      align-items: center;
      overflow: hidden;

      .left {
        display: none;
      }

      .text {
        width: 100px;
        height: 100%;
        overflow: hidden;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: start;
        flex: 1;
        margin-bottom: 0px;

        .textbox {
          display: flex;
          justify-content: center;
          align-items: center;

          a {
            font-size: 25px;
            font-weight: 500;
            color: black;
            text-decoration: none;
            margin: 0 5px 5px 5px;
          }

          h3 {
            font-size: 0px;
          }
        }
      }

      .right {
        display: none;
      }
    }
  }
}
