@import "../../global.scss";
@font-face {
  font-family: Stigma;
  src: url("../../Stigma Display Typeface.ttf");
}

.Spotlight {
  height: auto;
  width: 100%;
  background-color: $bgColour;
  display: flex;
  align-items: center;
  flex-direction: column;
  justify-content: center;
  overflow: hidden;
  margin-top: 100px;
  margin-bottom: 200px;

  text-decoration: none;

  a,
  a:link,
  a:visited,
  a:hover,
  a:active {
    text-decoration: "none";
  }

  h1 {
    font-family: Stigma;
    font-size: 80px;
    font-weight: 100;
    overflow: hidden;
    margin-bottom: 0px;
  }

  .container {
    width: -moz-fit-content;
    width: fit-content;
    height: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;

    .cardE {
      height: auto;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      overflow: hidden;
      z-index: -10;
      margin-bottom: 30px;

      .logoE {
        width: 100px;
        height: 100px;
        background-color: rgba(255, 255, 255, 0.85);
        overflow: hidden;
        align-self: flex-start;
        margin-bottom: -60px;
        z-index: 1;
        box-shadow: 0px 0px 15px -8px black;
      }

      .imgE {
        width: calc(50vw + 100px);
        height: calc(370px - 10vw);
        z-index: -2;

        opacity: 0.8;
        object-fit: cover;
        margin-top: -150px;
        margin-right: 50px;
      }

      .textE {
        width: 50vw;
        height: auto;
        position: relative;
        font-size: 20px;
        background-color: rgba(255, 255, 255, 0.85);
        color: black;
        overflow: hidden;
        z-index: 0;
        padding: 15px 25px 25px 85px;
        margin-left: 15px;

        .bottom {
          text-decoration: none;
        }

        .TitleE {
          overflow: hidden;
          z-index: 1;
          text-align: left;
          font-size: 40px;
        }

        .sTitleE {
          overflow: hidden;
          z-index: 1;
          text-align: left;
          font-size: 20px;
          margin-bottom: 15px;
        }
      }
    }

    .cardO {
      width: inherit;
      display: flex;
      align-items: center;
      justify-content: center;
      flex-direction: column;
      overflow: hidden;
      z-index: -10;
      margin-bottom: 30px;

      .logoO {
        width: 100px;
        height: 100px;
        background-color: rgba(250, 250, 250, 0.85);
        overflow: hidden;
        align-self: flex-end;
        margin-bottom: -60px;
        z-index: 1;
        box-shadow: 0px 0px 15px -8px black;
      }
      .TitleO {
        overflow: hidden;
        z-index: 1;
        text-align: right;
        font-size: 40px;
      }

      .sTitleO {
        overflow: hidden;
        z-index: 1;
        text-align: right;
        font-size: 20px;
        margin-bottom: 15px;
      }
      .imgO {
        width: calc(50vw + 100px);
        height: calc(370px - 10vw);
        z-index: -2;
        opacity: 0.8;
        object-fit: cover;
        margin-top: -150px;
        margin-left: 50px;
      }

      .textO {
        width: 50vw;
        height: fit-content;
        position: relative;
        font-size: 20px;
        background-color: rgba(255, 255, 255, 0.85);
        color: black;
        overflow: hidden;
        z-index: 0;
        padding: 15px 85px 25px 25px;
        margin-right: 15px;
        text-align: right;
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  .Spotlight {
    h1 {
      font-size: 16vw;
    }

    .container {
      margin-top: 20px;

      .cardE {
        .logoE {
          display: flex;
          align-self: center;
          justify-content: center;
          margin-bottom: -25px;
        }

        .imgE {
          display: none;
        }

        .textE {
          width: 70vw;
          padding: 35px 15px 15px 15px;

          .TitleE {
            font-size: 25px;
          }

          .sTitleE {
            font-size: 15px;
          }
        }
      }

      .cardO {
        .logoO {
          display: flex;
          align-self: center;
          justify-content: center;
          margin-bottom: -25px;
        }
        .TitleO {
          font-size: 25px;
        }

        .sTitleO {
          font-size: 15px;
        }
        .imgO {
          display: none;
        }

        .textO {
          width: 70vw;
          margin-right: 0;
          padding: 35px 15px 15px 15px;
        }
      }
    }
  }
}
