@import "../../global.scss";
@font-face {
  font-family: Stigma;
  src: url("../../Stigma Display Typeface.ttf");
}

.About {
  height: 100%;
  width: 100%;

  background-color: $bgColour;
  color: $txtColour;
  font-family: Stigma;
  overflow: hidden;

  .content {
    height: 100%;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;

    .wrapper {
      width: -moz-fit-content;
      width: fit-content;
      position: relative;
      display: flex;
      justify-content: flex-start;

      font-family: Stigma;
      overflow: hidden;

      .left {
        width: 500px;
        display: flex;
        object-fit: contain;
        overflow: hidden;

        .botImg {
          position: relative;
          background-color: rgb(167, 167, 167);
          width: 400px;
          height: 500px;
          overflow: hidden;
          z-index: 0;
          border: none;
          object-fit: cover;
        }
        .topImg {
          position: absolute;
          display: block;
          margin-top: 40px;
          margin-left: 90px;

          background-color: rgb(228, 228, 228);
          width: 400px;
          height: 350px;
          z-index: 2;

          border: none;
          object-fit: cover;
        }
      }
      .right {
        font-size: 20px;
        width: 45vw;
        margin-left: 25px;

        .title {
          font-family: Stigma;
          font-size: 100px;
          z-index: 2;
          margin-top: 30px;
        }
      }
    }
  }
}

@media only screen and (max-width: 480px) {
  .About {
    height: auto;
    width: 100%;

    background-color: $bgColour;
    color: $txtColour;
    font-family: Stigma;
    overflow: hidden;

    .content {
      .wrapper {
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        display: flex;
        flex-direction: column;

        font-family: Stigma;
        overflow: hidden;

        .left {
          width: 70vw;
          display: flex;
          object-fit: contain;
          overflow: hidden;

          .botImg {
            display: none;
          }
          .topImg {
            position: relative;
            display: block;
            justify-content: center;
            align-items: center;
            top: 0;

            margin: 0;

            background-color: rgb(228, 228, 228);
            width: 80vw;
            height: 80vw;

            border: none;
            object-fit: cover;
          }
        }
        .right {
          font-size: 20px;
          width: 80vw;
          margin: 0;
          overflow: hidden;

          .title {
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: Stigma;
            font-size: 17vw;
            z-index: 2;
          }
          .text {
            text-align: center;
          }
        }
      }
    }
  }
}

@media only screen and (min-width: 480px) and (max-width: 1200px) {
  .About {
    height: auto;
    width: 100%;

    background-color: $bgColour;
    color: $txtColour;
    font-family: Stigma;
    overflow: hidden;

    .content {
      .wrapper {
        height: 100%;
        width: 100%;
        justify-content: center;
        align-items: center;
        overflow: hidden;
        position: relative;
        display: flex;
        flex-direction: column;

        font-family: Stigma;
        overflow: hidden;

        .left {
          width: 70vw;
          display: flex;
          object-fit: contain;
          overflow: hidden;

          justify-content: center;
          align-items: center;

          .botImg {
            display: none;
          }
          .topImg {
            position: relative;
            display: block;
            justify-content: center;
            align-items: center;
            top: 0;

            margin: 0;

            background-color: rgb(228, 228, 228);
            width: 55vw;
            height: 55vw;

            border: none;
            object-fit: cover;
          }
        }
        .right {
          font-size: 20px;
          width: 80vw;
          margin: 0;
          overflow: hidden;

          .title {
            display: flex;
            align-items: center;
            justify-content: center;
            font-family: Stigma;
            font-size: 10vw;
            z-index: 2;
          }
          .text {
            text-align: center;
          }
        }
      }
    }
  }
}
